import { useState, useEffect } from "react";
//API
import API from "../API";
// Helpers
import { isPersistedState } from "../helpers";

const initialState = {
  page: 0,
  results: [],
  total_pages: 0,
  total_results: 0,
};

// Always name custom hooks starting with "use", following with camelCase custom name
export const useHomeFetch = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [state, setState] = useState(initialState);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [isBtnClicked, setIsBtnClicked] = useState(false);

  console.log("Search Term:", searchTerm);

  const fetchMovies = async (searchTerm = "", page) => {
    try {
      setError(false);
      setLoading(true);

      const movies = await API.fetchMovies(searchTerm, page);
      //   console.log(movies);

      setState((prev) => ({
        ...movies,
        results:
          // TRUE condition: appends new movies to existing (prev) movies
          page > 1 ? [...prev.results, ...movies.results] : [...movies.results],
      }));
    } catch (error) {
      setError(true);
    }
    setLoading(false);
  };

  // Initial render and search
  useEffect(() => {
    if (!searchTerm) {
      const sessionState = isPersistedState("homeState");

      if (sessionState) {
        setState(sessionState);
        return;
      }
    }
    setState(initialState);
    fetchMovies(searchTerm, 1);
  }, [searchTerm]);

  // Load more...
  useEffect(() => {
    if (!isBtnClicked) return;

    fetchMovies(searchTerm, state.page + 1);

    console.log("Pages: ", state.page);

    setIsBtnClicked(false);

    // return () => {};
  }, [isBtnClicked, searchTerm, state.page]);

  console.log(searchTerm);
  //Write to Session Storage
  useEffect(() => {
    if (!searchTerm) sessionStorage.setItem("homeState", JSON.stringify(state));
  }, [searchTerm, state]);

  return { state, loading, error, searchTerm, setSearchTerm, setIsBtnClicked };
};
